.button {
    color: white;
    font-size: 24px;
    width: 100%;
    margin-right: 16px;
    background: dodgerblue;
    border: 0;
    border-radius: 5px;
    cursor: pointer;
}
.button:last-child {
    margin-right: 0;
}

.button:disabled {
    opacity: 0.3;
    cursor: default;
}